import {__} from '@wordpress/i18n';

import {
    PanelBody,
    BaseControl,
    SelectControl,
    RangeControl,
    TextControl,
    TextareaControl,
    Button,
    ToggleControl,
    Dashicon,
    Tip,
    __experimentalBoxControl as BoxControl,
} from '@wordpress/components';

import {
    MediaUploadCheck,
    MediaUpload,
    PanelColorSettings,
    InspectorControls,
    RichText,
    useBlockProps
} from '@wordpress/block-editor';

export default function Edit(props) {

    const {attributes, setAttributes} = props;

    const {
        heading,
        headingHTMLTag,
        tocAdded,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        headingColor,
        darkHeadingColor,
        description,
        descriptionColor,
        darkDescriptionColor,
        desktopDescriptionSize,
        tabletDescriptionSize,
        mobileDescriptionSize,
        image,
        imageURL,
        imageAlt,
        imageH,
        imageW,
        desktopImageSize,
        tabletImageSize,
        mobileImageSize,
        price,
        salePrice,
        desktopPriceSize,
        tabletPriceSize,
        mobilePriceSize,
        priceColor,
        darkPriceColor,
        ratingMeta,
        ratingPercent,
        ratingColor,
        darkRatingColor,
        ratingSize,
        link,
        buttonLabel,
        isButtonBorder,
        isInternal,
        isSponsored,
        desktopButtonSize,
        tabletButtonSize,
        mobileButtonSize,
        buttonColor,
        buttonBg,
        buttonHoverColor,
        buttonHoverBg,
        darkButtonColor,
        darkButtonBg,
        darkButtonHoverColor,
        darkButtonHoverBg,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        background,
        darkBackground,
        desktopPadding,
        tabletPadding,
        mobilePadding
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    function setMediaAttribute(name, value) {
        setAttributes({[name]: value.url});
        setAttributes({[name + 'Alt']: value.alt});
        setAttributes({[name + 'H']: value.height});
        setAttributes({[name + 'W']: value.width});
    }

    function removeMediaAttribute(name) {
        setAttributes({[name]: ''});
        setAttributes({[name + 'Alt']: ''});
        setAttributes({[name + 'H']: null});
        setAttributes({[name + 'W']: null});
    }

    const borderStyleOptions = [
        {label: __('None'), value: 'none'},
        {label: __('Solid'), value: 'solid'},
        {label: __('Dashed'), value: 'dashed'},
        {label: __('Dotted'), value: 'dotted'},
        {label: __('Double'), value: 'double'},
    ];

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    let wrapperClassName = `gb-wrap af-product`;
    if (isButtonBorder) {
        wrapperClassName += ` is-button-border`;
    }
    if (shadow) {
        wrapperClassName += ` yes-shadow`;
    }

    const blockProps = useBlockProps({
        className: wrapperClassName,
        style: {
            ...(headingColor && {'--heading-color': headingColor}),
            ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
            ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
            ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
            ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
            ...(descriptionColor && {'--description-color': descriptionColor}),
            ...(darkDescriptionColor && {'--dark-description-color': darkDescriptionColor}),
            ...(desktopDescriptionSize && {'--desktop-description-size': `${desktopDescriptionSize}px`}),
            ...(tabletDescriptionSize && {'--tablet-description-size': `${tabletDescriptionSize}px`}),
            ...(mobileDescriptionSize && {'--mobile-description-size': `${mobileDescriptionSize}px`}),
            ...(desktopImageSize && {'--desktop-image-size': `${desktopImageSize}px`}),
            ...(tabletImageSize && {'--tablet-image-size': `${tabletImageSize}px`}),
            ...(mobileImageSize && {'--mobile-image-size': `${mobileImageSize}px`}),
            ...(priceColor && {'--price-color': priceColor}),
            ...(darkPriceColor && {'--dark-price-color': darkPriceColor}),
            ...(desktopPriceSize && {'--desktop-price-size': `${desktopPriceSize}px`}),
            ...(tabletPriceSize && {'--tablet-price-size': `${tabletPriceSize}px`}),
            ...(mobilePriceSize && {'--mobile-price-size': `${mobilePriceSize}px`}),
            ...(ratingColor && {'--rating-color': ratingColor}),
            ...(darkRatingColor && {'--dark-rating-color': darkRatingColor}),
            ...(ratingSize && {'--rating-size': `${ratingSize}px`}),
            ...(buttonColor && {'--button-color': buttonColor}),
            ...(buttonBg && {'--button-bg': buttonBg}),
            ...(darkButtonColor && {'--dark-button-color': darkButtonColor}),
            ...(darkButtonBg && {'--dark-button-bg': darkButtonBg}),
            ...(buttonHoverColor && {'--button-hover-color': buttonHoverColor}),
            ...(buttonHoverBg && {'--button-hover-bg': buttonHoverBg}),
            ...(darkButtonHoverColor && {'--dark-button-hover-color': darkButtonHoverColor}),
            ...(darkButtonHoverBg && {'--dark-button-hover-bg': darkButtonHoverBg}),
            ...(desktopButtonSize && {'--desktop-button-size': `${desktopButtonSize}px`}),
            ...(tabletButtonSize && {'--tablet-button-size': `${tabletButtonSize}px`}),
            ...(mobileButtonSize && {'--mobile-button-size': `${mobileButtonSize}px`}),
            ...(borderStyle && {'--border-style': borderStyle}),
            ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
            ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
            ...(borderColor && {'--border-color': borderColor}),
            ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
            ...(background && {'--bg': background}),
            ...(darkBackground && {'--dark-bg': darkBackground}),
            ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
            ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
            ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
        },
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('Details')} initialOpen={true}>
                    <SelectControl
                        label={__('Heading Tag')}
                        value={headingHTMLTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingHTMLTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                    <BaseControl label={__('Product Image')}>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setMediaAttribute('image', value)}
                                allowedTypes={['image']}
                                value={image}
                                render={({open}) => (
                                    <div>
                                        {!image && (
                                            <Button className="button button-large" onClick={open}>
                                                {__('Add Image')}
                                            </Button>
                                        )}
                                        {image && (
                                            <div>
                                                <img src={image} alt="image"/>
                                                <Button className="button button-large" onClick={() => removeMediaAttribute('image')}>
                                                    {__('Remove Image')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </BaseControl>
                    <TextareaControl
                        label={__('or Enter Custom Link')}
                        value={imageURL}
                        onChange={(value) => setAttribute('imageURL', value)}
                        placeholder={__('//website.com/.../image.jpg')}
                    />
                    <TextControl
                        label={__('Price')}
                        value={price}
                        onChange={(value) => setAttribute('price', value)}
                    />
                    <TextControl
                        label={__('Discounted Price')}
                        value={salePrice}
                        onChange={(value) => setAttribute('salePrice', value)}
                    />
                    <RangeControl
                        label={__('Rating Percent')}
                        value={ratingPercent}
                        onChange={(value) => setAttribute('ratingPercent', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <TextControl
                        label={__('Rating Meta')}
                        placeholder={__('Good Choose')}
                        value={ratingMeta}
                        onChange={(value) => setAttribute('ratingMeta', value)}
                    />
                    <TextControl
                        label={__('Affiliate Link')}
                        placeholder={__('https://...')}
                        value={link}
                        onChange={(value) => setAttribute('link', value)}
                        help={__('Input the full link, including the HTTPS protocol')}
                    />
                    <TextControl
                        label={__('Button Label')}
                        value={buttonLabel}
                        onChange={(value) => setAttribute('buttonLabel', value)}
                    />
                    <ToggleControl
                        label={__('Internal Link?')}
                        checked={isInternal}
                        onChange={(value) => setAttribute('isInternal', value)}
                        help={__('Disable opening in a new tab and remove the nofollow,sponsored attributes in the rel for internal links.')}
                    />
                    <ToggleControl
                        label={__('Sponsored?')}
                        checked={isSponsored}
                        onChange={(value) => setAttribute('isSponsored', value)}
                        help={__('Tell the search engine bot your relationship with the linked page.')}
                    />
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Heading')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="18"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="17"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="16"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: headingColor,
                                onChange: (value) => setAttribute('headingColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkHeadingColor,
                                onChange: (value) => setAttribute('darkHeadingColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Description')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopDescriptionSize}
                                onChange={(value) => setAttribute('desktopDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletDescriptionSize}
                                onChange={(value) => setAttribute('tabletDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileDescriptionSize}
                                onChange={(value) => setAttribute('mobileDescriptionSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: descriptionColor,
                                onChange: (value) => setAttribute('descriptionColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkDescriptionColor,
                                onChange: (value) => setAttribute('darkDescriptionColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Image')} initialOpen={false}>
                    <label className="rb-control-label">{__('Image Width')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="150"
                                value={desktopImageSize}
                                onChange={(value) => setAttribute('desktopImageSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="100"
                                value={tabletImageSize}
                                onChange={(value) => setAttribute('tabletImageSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="100"
                                value={mobileImageSize}
                                onChange={(value) => setAttribute('mobileImageSize', value)}
                            />
                        </div>
                    </div>
                </PanelBody>
                <PanelBody title={__('Rating')} initialOpen={false}>
                    <RangeControl
                        label={__('Size')}
                        value={ratingSize}
                        onChange={(value) => setAttribute('ratingSize', value)}
                        step={1}
                        max={50}
                        min={10}
                    />
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: ratingColor,
                                onChange: (value) => setAttribute('ratingColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkRatingColor,
                                onChange: (value) => setAttribute('darkRatingColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Price')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopPriceSize}
                                onChange={(value) => setAttribute('desktopPriceSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletPriceSize}
                                onChange={(value) => setAttribute('tabletPriceSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobilePriceSize}
                                onChange={(value) => setAttribute('mobilePriceSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: priceColor,
                                onChange: (value) => setAttribute('priceColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkPriceColor,
                                onChange: (value) => setAttribute('darkPriceColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('CTA Button')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopButtonSize}
                                onChange={(value) => setAttribute('desktopButtonSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletButtonSize}
                                onChange={(value) => setAttribute('tabletButtonSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileButtonSize}
                                onChange={(value) => setAttribute('mobileButtonSize', value)}
                            />
                        </div>
                    </div>
                    <ToggleControl
                        label={__('Border Style')}
                        checked={isButtonBorder}
                        onChange={(value) => setAttribute('isButtonBorder', value)}
                        help={__('Select border style for the button. Background color settings will be considered as the border color.')}
                    />
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: buttonColor,
                                onChange: (value) => setAttribute('buttonColor', value),
                                label: __('Color'),
                            },
                            {
                                value: buttonBg,
                                onChange: (value) => setAttribute('buttonBg', value),
                                label: __('Background'),
                            },
                            {
                                value: buttonHoverColor,
                                onChange: (value) => setAttribute('buttonHoverColor', value),
                                label: __('Hover - Color'),
                            },
                            {
                                value: buttonHoverBg,
                                onChange: (value) => setAttribute('buttonHoverBg', value),
                                label: __('Hover - Background'),
                            },
                        ]}
                    />
                    <PanelColorSettings
                        title={__('Dark Mode - Color')}
                        colorSettings={[
                            {
                                value: darkButtonColor,
                                onChange: (value) => setAttribute('darkButtonColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkButtonBg,
                                onChange: (value) => setAttribute('darkButtonBg', value),
                                label: __('Background'),
                            },
                            {
                                value: darkButtonHoverColor,
                                onChange: (value) => setAttribute('darkButtonHoverColor', value),
                                label: __('Hover - Color'),
                            },
                            {
                                value: darkButtonHoverBg,
                                onChange: (value) => setAttribute('darkButtonHoverBg', value),
                                label: __('Hover - Background'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Border & Shadow')} initialOpen={false}>
                    <SelectControl
                        label={__('Border Style')}
                        value={borderStyle}
                        options={borderStyleOptions}
                        onChange={(value) => setAttribute('borderStyle', value)}
                        help={__('Please select the border color to see the border if you enable it.')}
                    />
                    {borderStyle && borderStyle !== 'none' && (
                        <>
                            <BoxControl
                                resetValues={{top: null, right: null, bottom: null, left: null}}
                                label={__('Border Width')}
                                values={borderWidth}
                                onChange={(value) => setAttribute('borderWidth', value)}
                            />
                            <PanelColorSettings
                                title={__('Border Color')}
                                colorSettings={[
                                    {
                                        value: borderColor,
                                        onChange: (value) => setAttribute('borderColor', value),
                                        label: __('Color'),
                                    },
                                    {
                                        value: darkBorderColor,
                                        onChange: (value) => setAttribute('darkBorderColor', value),
                                        label: __('Dark Mode - Color'),
                                    },
                                ]}
                            />
                        </>
                    )}
                    <RangeControl
                        label={__('Border Radius')}
                        value={borderRadius}
                        onChange={(value) => setAttribute('borderRadius', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <ToggleControl
                        label={__('Shadow')}
                        checked={shadow}
                        onChange={(value) => setAttribute('shadow', value)}
                    />
                    <PanelColorSettings
                        title={__('Background')}
                        colorSettings={[
                            {
                                value: background,
                                onChange: (value) => setAttribute('background', value),
                                label: __('Background'),
                            },
                            {
                                value: darkBackground,
                                onChange: (value) => setAttribute('darkBackground', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Inner Padding')} initialOpen={false}>
                    <BaseControl><Tip>{__('These settings will apply to block wrapper. Click outside to see the update!')}</Tip></BaseControl>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '30px', right: '30px', bottom: '30px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop')}</>}
                            values={desktopPadding}
                            onChange={(value) => setAttribute('desktopPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '25px', right: '25px', bottom: '25px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet')}</>}
                            values={tabletPadding}
                            onChange={(value) => setAttribute('tabletPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '20px', right: '20px', bottom: '20px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile')}</>}
                            values={mobilePadding}
                            onChange={(value) => setAttribute('mobilePadding', value)}
                        />
                    </div>
                </PanelBody>
            </InspectorControls>
            <div className="af-inner">
                {image ? (
                    <img className="af-image" src={image} alt="Product"/>
                ) : imageURL ? (
                    <img className="af-image" src={imageURL} alt="Product"/>
                ) : null}
                <div className="af-content">
                    <RichText
                        tagName={headingHTMLTag || 'h3'}
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        className="af-heading gb-heading"
                        placeholder="Enter heading text..."
                    />
                    <RichText
                        tagName="p"
                        value={description}
                        onChange={(value) => setAttribute('description', value)}
                        className="af-description gb-description"
                        placeholder="Enter description text..."
                    />
                    {(ratingPercent || ratingMeta) && (
                        <div className="af-rating">
                            {ratingPercent && (
                                <span className="rstar-wrap">
                            <span className="rstar-bg" style={{width: `${ratingPercent}%`}}></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                        </span>
                            )}
                            {ratingMeta && (
                                <RichText.Content
                                    tagName="span"
                                    value={ratingMeta}
                                    className="af-rating-meta h5"
                                />
                            )}
                        </div>
                    )}
                </div>
                <div className="af-cta-wrap">
                    {salePrice ? (
                        <span className="af-price h3"><del>{price}</del>
                            {salePrice}</span>
                    ) : (
                        <span className="af-price h3">{price}</span>
                    )}
                    <RichText
                        tagName="span"
                        value={buttonLabel}
                        onChange={(value) => setAttribute('buttonLabel', value)}
                        className="af-button is-btn"
                        placeholder="See It"
                    />
                </div>
            </div>
        </div>
    )
}
