import {useBlockProps, RichText} from '@wordpress/block-editor';

export default function save(props) {
    const {attributes} = props;

    const {
        heading,
        headingHTMLTag,
        tocAdded,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        headingColor,
        darkHeadingColor,
        description,
        descriptionColor,
        darkDescriptionColor,
        desktopDescriptionSize,
        tabletDescriptionSize,
        mobileDescriptionSize,
        image,
        imageURL,
        imageAlt,
        imageH,
        imageW,
        desktopImageSize,
        tabletImageSize,
        mobileImageSize,
        price,
        salePrice,
        desktopPriceSize,
        tabletPriceSize,
        mobilePriceSize,
        priceColor,
        darkPriceColor,
        ratingMeta,
        ratingPercent,
        ratingColor,
        darkRatingColor,
        ratingSize,
        link,
        buttonLabel,
        isButtonBorder,
        isInternal,
        isSponsored,
        desktopButtonSize,
        tabletButtonSize,
        mobileButtonSize,
        buttonColor,
        buttonBg,
        buttonHoverColor,
        buttonHoverBg,
        darkButtonColor,
        darkButtonBg,
        darkButtonHoverColor,
        darkButtonHoverBg,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        background,
        darkBackground,
        desktopPadding,
        tabletPadding,
        mobilePadding
    } = attributes;

    let wrapperClassName = `gb-wrap af-product`;
    if (isButtonBorder) {
        wrapperClassName += ` is-button-border`;
    }
    if (shadow) {
        wrapperClassName += ` yes-shadow`;
    }
    if (salePrice) {
        wrapperClassName += ` yes-sale`;
    }

    const blockProps = useBlockProps.save(
        {
            className: wrapperClassName,
            style: {
                ...(headingColor && {'--heading-color': headingColor}),
                ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
                ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
                ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
                ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
                ...(descriptionColor && {'--description-color': descriptionColor}),
                ...(darkDescriptionColor && {'--dark-description-color': darkDescriptionColor}),
                ...(desktopDescriptionSize && {'--desktop-description-size': `${desktopDescriptionSize}px`}),
                ...(tabletDescriptionSize && {'--tablet-description-size': `${tabletDescriptionSize}px`}),
                ...(mobileDescriptionSize && {'--mobile-description-size': `${mobileDescriptionSize}px`}),
                ...(desktopImageSize && {'--desktop-image-size': `${desktopImageSize}px`}),
                ...(tabletImageSize && {'--tablet-image-size': `${tabletImageSize}px`}),
                ...(mobileImageSize && {'--mobile-image-size': `${mobileImageSize}px`}),
                ...(priceColor && {'--price-color': priceColor}),
                ...(darkPriceColor && {'--dark-price-color': darkPriceColor}),
                ...(desktopPriceSize && {'--desktop-price-size': `${desktopPriceSize}px`}),
                ...(tabletPriceSize && {'--tablet-price-size': `${tabletPriceSize}px`}),
                ...(mobilePriceSize && {'--mobile-price-size': `${mobilePriceSize}px`}),
                ...(ratingColor && {'--rating-color': ratingColor}),
                ...(darkRatingColor && {'--dark-rating-color': darkRatingColor}),
                ...(ratingSize && {'--rating-size': `${ratingSize}px`}),
                ...(buttonColor && {'--button-color': buttonColor}),
                ...(buttonBg && {'--button-bg': buttonBg}),
                ...(darkButtonColor && {'--dark-button-color': darkButtonColor}),
                ...(darkButtonBg && {'--dark-button-bg': darkButtonBg}),
                ...(buttonHoverColor && {'--button-hover-color': buttonHoverColor}),
                ...(buttonHoverBg && {'--button-hover-bg': buttonHoverBg}),
                ...(darkButtonHoverColor && {'--dark-button-hover-color': darkButtonHoverColor}),
                ...(darkButtonHoverBg && {'--dark-button-hover-bg': darkButtonHoverBg}),
                ...(desktopButtonSize && {'--desktop-button-size': `${desktopButtonSize}px`}),
                ...(tabletButtonSize && {'--tablet-button-size': `${tabletButtonSize}px`}),
                ...(mobileButtonSize && {'--mobile-button-size': `${mobileButtonSize}px`}),
                ...(borderStyle && {'--border-style': borderStyle}),
                ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
                ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
                ...(borderColor && {'--border-color': borderColor}),
                ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
                ...(background && {'--bg': background}),
                ...(darkBackground && {'--dark-bg': darkBackground}),
                ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
                ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
                ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
            },
        }
    );

    return (
        <div {...blockProps}>
            <a href={link}
               target={isInternal ? null : "_blank"}
               rel={isInternal ? null : (isSponsored ? "sponsored noopener" : "noopener noreferrer nofollow")}
               className="af-link"
            ></a>
            <div className="af-inner">
                {image ? (
                    <img loading="lazy" className="af-image" src={image} height={imageH || ""} width={imageW || ""} alt={imageAlt || ""}/>
                ) : imageURL ? (
                    <img loading="lazy" className="af-image" src={imageURL} alt=""/>
                ) : null}
                <div className="af-content">
                    <RichText.Content
                        tagName={headingHTMLTag || 'h3'}
                        value={heading}
                        className={`af-heading gb-heading${!tocAdded ? ' none-toc' : ''}`}
                    />
                    <RichText.Content
                        tagName="p"
                        value={description}
                        className="af-description gb-description"
                    />
                    {(ratingPercent || ratingMeta) && (
                        <div className="af-rating">
                            {ratingPercent && (
                                <span className="rstar-wrap">
                            <span className="rstar-bg" style={{width: `${ratingPercent}%`}}></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                            <span className="rstar"><i className="rbi rbi-star" aria-hidden="true"></i></span>
                        </span>
                            )}
                            {ratingMeta && (
                                <RichText.Content
                                    tagName="span"
                                    value={ratingMeta}
                                    className="af-rating-meta h5"
                                />
                            )}
                        </div>
                    )}
                </div>
                <div className="af-cta-wrap">
                    {salePrice ? (
                        <span className="af-price h3"><del>{price}</del>
                            {salePrice}</span>
                    ) : (
                        <span className="af-price h3">{price}</span>
                    )}
                    <a
                        href={link}
                        target={isInternal ? null : "_blank"}
                        rel={isInternal ? null : (isSponsored ? "sponsored noopener" : "noopener noreferrer nofollow")}
                        className="af-button is-btn">
                        {buttonLabel}
                    </a>
                </div>
            </div>
        </div>
    )
}